import { useEffect, useState } from "react";

import { usePrevious } from "../../utils/hooks/use-previous";

/**
 * @description manages the calendar's offset via the dayzed offset property
 * @param dateInView
 */
const useCalendarOffset = (
	dateInView?: Date,
) => {
	const previousDateInView = usePrevious(dateInView);

	const [offset, setOffset] = useState<number>(0);

	useEffect(() => {
		if (previousDateInView !== dateInView) {
			setOffset(0);
		}
	}, [previousDateInView, dateInView, offset]);

	return {
		offset,
		setOffset,
	};
};

export default useCalendarOffset;
